/*
 * @  SHW
 * @ wait 
 */
const throttle = function () {
	let previous = 0
	return function (wait) {
		return new Promise(resolve => {
			let now = Date.now()
			if (now - previous > wait) {
				previous = now
				resolve()
			}
		})
	}
}

/*
 * @  SHW
 * @ wait 
 * @ immediate 
 */
const debounce = function () {
	let timeout = null
	return function (wait, immediate = true) {
		return new Promise(resolve => {
			if (timeout) {
				clearTimeout(timeout)
			}
			if (immediate) {
				let immediateAction = !timeout
				timeout = setTimeout(() => {
					timeout = null
				}, wait)
				if (immediateAction) {
					resolve()
				}
			} else {
				timeout = setTimeout(() => {
					resolve()
				}, wait)
			}
		})
	}
}
/*
 * @  SHW
 * @ number 
 * @ len 
 * @ pushZero 
 */
const toFixedSHW = function (number, len = 0, pushZero = true) {
	if (isNaN(number)) {
		return number
	}
	let num = number - 0 + 0.0000000001
	let _len = len - 0
	let mathPow = Math.pow(10, _len)
	let _num = Math.round(num * mathPow) / mathPow + ''
	if (pushZero && _len > 0) {
		let dot = _num.indexOf('.')
		if (!~dot) {
			_num += '.'
			dot = _num.length - 1
		}
		// ,_len,0
		let dotLen = _num.length - (dot + 1)
		// 0
		let zeroLen = _len - dotLen
		if (dotLen < _len) {
			for (let i = 0; i < zeroLen; i++) {
				_num += '0'
			}
		}
	}
	return _num
}
export {
	throttle,
	debounce,
	toFixedSHW
}
