<template>
	<form action="">
		<Search
			class="search-pro"
			v-model="searchKey"
			:placeholder="placeholder"
			:shape="shape"
			:clearable="clearable"
			@search="onSearch"
			@input="onSearchInput"
			@focus="onSearchFocus"
			@blur="onSearchBlur"
			@clear="onSearchClear"
			@cancel="onSearchCancel"
		/>
	</form>
</template>

<script type="text/babel">
/**
 * SearchPro by SHW 2020/4/29
 * @desc vant-search
 *
 * @param {value}  [any] - 
 * @param {type}  [String] - field 
 * @param {disabled}  [Boolean] - 
 *
 * @event {change} 	
 * @event {blur} 
 * @event {focus} 
 *
 */
import { Search } from 'vant'
export default {
	name: 'van-pro-search',
	components: {
		Search: Search
	},
	props: {
		value: null,
		placeholder: {
			type: String,
			default: ''
		},
		shape: {
			type: String,
			default: 'square'
		},
		clearable: {
			type: Boolean,
			default: false
		},
		duration: {
			type: Number,
			default: 500
		},
		searchImmediate: {
			type: Boolean,
			default: false
		}
	},
	data () {
		return {
			searchDebounce: this.$fxUtils.debounce(),
			searchKey: ''
		}
	},
	watch: {
		value: {
			immediate: true,
			handler (val) {
				this.searchKey = val
			}
		}
	},
	methods: {
		onSearch (searchKey) {
			this.$emit('input', searchKey)
			this.$emit('search', searchKey)
		},
		onSearchInput (searchKey) {
			this.searchDebounce(this.duration, this.searchImmediate).then(() => {
				this.$emit('input', searchKey)
				this.$emit('search', searchKey)
			})
		},
		onSearchFocus () {
			this.$emit('focus')
		},
		onSearchBlur () {
			this.$emit('blur')
		},
		onSearchClear () {
			this.$emit('clear')
		},
		onSearchCancel () {
			this.$emit('cancel')
		}
	}
}
</script>
<style scoped lang="stylus">
.search-pro {
	>>>.van-cell {
		$fx-dynamic-font-size(14px)
	}
}
</style>
