/**
 * VirtualTableColumn by shang 2019/10/25
 * @desc 
 * @param {prop}  [String] - key
 * @param {label}  [String] - 
 * @param {align}  [String] - 
 * @param {sort}  [String] -  up, down
 * @param {decimal}  [Number] - 
 * @param {formatter}  [Function] - value
 * @param {visiable}  [Boolean] - 
 */
export default {
	name: 'windTableColumn',
	props: {
		prop: String,
		label: String,
		align: {
			type: String,
			default: 'center'
		},
		sort: String,
		decimal: Number,
		formatter: Function,
		visiable: {
			type: Boolean,
			default: true
		}
	},
	data () {
		return {
			currentSort: 'up'
		}
	},
	created () {
		this.currentSort = this.sort
	},
	mounted () {
		this.$parent.columnArr.push(this)
	},
	methods: {
		setSort (val) {
			this.currentSort = val
		},
		getSort () {
			return this.currentSort
		}
	},
	render (h) {
		return h('div', this.$slots.default)
	}
}
