<template>
	<DatetimePicker
		v-model="$attrs.currentDate"
		:type="type"
		:formatter="dataPickerFormatter"
		:min-date="mindate"
		:max-date="maxdate"
		@cancel="onCancelClick"
		v-on="$listeners"
	/>
</template>

<script>
import { DatetimePicker } from 'vant'
export default {
	name: 'w-date-picker',
	components: {
		DatetimePicker
	},
	props: {
		visibleFlag: Boolean,
		type: {
			type: String,
			default: 'datetime'
		},
		mindate: {
			type: Date,
			default: () => {
				return new Date((new Date()).getFullYear() - 10, 0, 1)
			}
		},
		maxdate: {
			type: Date,
			default: () => {
				return new Date((new Date()).getFullYear() + 10, 11, 31)
			}
		}
	},
	methods: {
		dataPickerFormatter (type, value) {
			if (type === 'year') {
				return `${value}`
			} else if (type === 'month') {
				return `${value}`
			} else if (type === 'day') {
				return `${value}`
			} else if (type === 'hour') {
				return `${value}`
			} else if (type === 'minute') {
				return `${value}`
			}
		},
		onCancelClick () {
			this.$emit('update:visibleFlag', false)
		}
	}
}
</script>
