// cordova-plugin-camera
import app from '@/main.js'
//  
const getPictureByCamera = function () {
	return new Promise((resolve, reject) => {
		try {
			const cameraOptions = {
				destinationType: navigator.camera.DestinationType.DATA_URL,
				quality: 30,
				correctOrientation: true
			}
			navigator.camera.getPicture(result => {
				resolve(result)
			}, (err) => {
				if (err === 'No Image Selected') {
					return false
				} else {
					app.vue.$fxToast.fail('')
				}
			}, cameraOptions)
		} catch (error) {
			console.warn('camera.getPictureByCamera')
			app.vue.$fxToast.fail('')
		}
	})
}
//  
const getPictureByLibrary = function () {
	return new Promise((resolve, reject) => {
		try {
			const cameraOptions = {
				destinationType: navigator.camera.DestinationType.DATA_URL,
				sourceType: navigator.camera.PictureSourceType.PHOTOLIBRARY,
				mediaType: 0,
				quality: 30,
				targetWidth: 800,
				targetHeight: 800
			}
			navigator.camera.getPicture(result => {
				resolve(result)
			}, (err) => {
				if (err === 'No Image Selected') {
					return false
				} else {
					app.vue.$fxToast.fail('')
				}
			}, cameraOptions)
		} catch (error) {
			console.warn('camera.getPictureByLibrary')
			app.vue.$fxToast.fail('')
		}
	})
}
export default {
	getPictureByCamera,
	getPictureByLibrary
}
